import React, { useCallback, useEffect, useState } from 'react'
import { Box, SelectChangeEvent, Typography } from '@mui/material'
import { Tabs, Tab, TabPanel, ControlledSelectField } from '../../../Sub/FormElements'
import AchievementItem from '../AchievementItem/AchievementItem'
import AchievementPopup from '../AchievementPopup/AchievementPopup'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import LoadingOverlay from '../../../Sub/DataTable/LoadingOverlay/LoadingOverlay'
import achievementFiltersArray from '../../../../utils/achievmentFiltersArray'
import { TAchievement } from '../../../../services/types/achievements/achievements-types'
import { filterAchievements } from '../../../../services/slices/achievements-slice'

const AchievementsList = () => {
  const {
    status,
    filteredAllAchievements,
    filteredCommonAchievements,
    filteredPreaccelerationAchievements,
    filteredAccelerationAchievements,
    filteredTestPeriodAchievements,
    filteredCommunityAchievements,
  } = useAppSelector((store) => store.achievements)
  const [tabValue, setTabValue] = useState(0)
  const [filter, setFilter] = useState(1)
  const dispatch = useAppDispatch()
  const isLoading = status === 'pending'

  const handleTabChange = useCallback((event, newValue) => {
    setTabValue(newValue)
  }, [])

  useEffect(() => {
    dispatch(filterAchievements(filter))
  }, [filter])

  const handleChangeFilter = (e: SelectChangeEvent) => {
    const id = parseInt(e.target.value, 10)
    setFilter(id)
  }

  return (
    <>
      {isLoading && <LoadingOverlay />}
      <AchievementPopup />
      <Box sx={{ paddingTop: '30px', minHeight: '500px', maxWidth: 1200, minWidth: 1200, position: 'relative' }}>
        <Box sx={{ width: '187px', position: 'absolute', top: '77px', right: 0, zIndex: 100 }}>
          <ControlledSelectField
            maxWidth={187}
            value={filter}
            onChange={handleChangeFilter}
            items={achievementFiltersArray}
          />
        </Box>
        <Typography variant="h2" sx={{ paddingLeft: '20px' }}>
          Достижения
        </Typography>
        <Tabs
          value={tabValue}
          onChange={handleTabChange}
          sx={{
            minHeight: 80,
            padding: '22px 20px',
            boxSizing: 'border-box',
            '& .MuiTabs-flexContainer': {
              gap: '20px',
            },
            '&.MuiTab-root': {
              fontFamily: ['YS Text', '-apple-system', 'BlinkMacSystemFont', 'Arial', 'Helvetica', 'sans-serif'].join(
                ',',
              ),
              fontSize: 18,
            },
          }}
        >
          <Tab label="Все" variant="response" />
          <Tab label="Общие" variant="response" />
          <Tab label="Преакселерация" variant="response" />
          <Tab label="Акселерация" variant="response" />
          <Tab label="Испытательный срок" variant="response" />
          <Tab label="Сообщество" variant="response" />
        </Tabs>
        <TabPanel value={tabValue} index={0}>
          {filteredAllAchievements.map((achievement) => (
            <AchievementItem data={achievement} key={achievement.uuid} />
          ))}
        </TabPanel>
        <TabPanel value={tabValue} index={1}>
          {filteredCommonAchievements.map((achievement) => (
            <AchievementItem data={achievement} key={achievement.title} />
          ))}
        </TabPanel>
        <TabPanel value={tabValue} index={2}>
          {filteredPreaccelerationAchievements.map((achievement) => (
            <AchievementItem data={achievement} key={achievement.uuid} />
          ))}
        </TabPanel>
        <TabPanel value={tabValue} index={3}>
          {filteredAccelerationAchievements.map((achievement) => (
            <AchievementItem data={achievement} key={achievement.uuid} />
          ))}
        </TabPanel>
        <TabPanel value={tabValue} index={4}>
          {filteredTestPeriodAchievements.map((achievement) => (
            <AchievementItem data={achievement} key={achievement.uuid} />
          ))}
        </TabPanel>
        <TabPanel value={tabValue} index={5}>
          {filteredCommunityAchievements.map((achievement) => (
            <AchievementItem data={achievement} key={achievement.uuid} />
          ))}
        </TabPanel>
      </Box>
    </>
  )
}

export default React.memo(AchievementsList)
