import React, { FC, useEffect, useState } from 'react'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import { ThemeProvider, Typography, IconButton, Button, Dialog, DialogContent, DialogTitle } from '@mui/material'
import { AxiosError } from 'axios'
import IconClose from '../../../../../assets/Icons/IconClose/IconClose'
import useStyles from './AppointmentConsultationPopup.styles'
import api from '../../../../../api/api'
import useAsync from '../../../../../hooks/useAsync'
import { newConsultSchema } from '../../../../../utils/schemas'
import { API_CONSULTATION_ENDPOINT, API_CONSULTANT_DESCRIPTION_ENDPOINT } from '../../../../../utils/constants'
import theme from '../../../../../utils/themeMUI5'
import { ControlledSelectField, ControlledTextField } from '../../../../Sub/FormElements'
import processServerError from '../../../../../utils/server-error'
import { TSetSnack } from '../../../../../services/types/global-types'

type Props = {
  open: boolean
  setOpen: React.Dispatch<React.SetStateAction<boolean>>
  setSnack: TSetSnack
}

type TFormData = {
  consultation_theme: number | string
  problem_description: string
}

const AppointmentConsultationPopup: FC<Props> = ({ open, setOpen, setSnack }) => {
  const [consultants, setConsultants] = useState([])
  const defaultValues = {}

  const { run } = useAsync({
    data: null,
    error: null,
  })
  const {
    control,
    handleSubmit,
    formState: { errors },
    setError,
    reset,
    setValue,
    watch,
  } = useForm<TFormData>({ defaultValues, resolver: yupResolver(newConsultSchema), mode: 'onChange' })

  const classes = useStyles()
  const consultantThemeValue = watch('consultation_theme')
  const handleClose = () => {
    setOpen(false)
    reset(defaultValues)
  }

  const handleConsultantThemeChange = async (e: React.ChangeEvent<{ name?: string | undefined; value: string }>) => {
    setValue('consultation_theme', e.target.value, { shouldValidate: true, shouldDirty: true })
  }

  const getConsultants = async () => {
    try {
      const response = await api.get(API_CONSULTANT_DESCRIPTION_ENDPOINT)
      setConsultants(response.data)
      return Promise.resolve(response.data)
    } catch (e) {
      const err = e as AxiosError
      return Promise.reject(err.response)
    }
  }

  useEffect(() => {
    if (open) run(getConsultants())
  }, [open])

  const onCreateConsult = async (data: TFormData) => {
    try {
      const response = await api.post(API_CONSULTATION_ENDPOINT, { ...data })
      if (response.status === 201) {
        setSnack({
          open: true,
          header: 'Заявка отправлена',
          message: 'Консультант свяжется с тобой в течение трех рабочих дней',
        })
        reset(defaultValues)
        handleClose()
      }
      return Promise.resolve(response.data)
    } catch (e) {
      return processServerError(e, setError)
    }
  }

  const onSubmit = (data: TFormData) => {
    run(onCreateConsult(data))
  }

  return (
    <ThemeProvider theme={theme}>
      <Dialog
        maxWidth="xl"
        classes={{ paper: classes.root }}
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <IconButton onClick={handleClose} className={classes.closePopup}>
          <IconClose />
        </IconButton>
        <DialogTitle mb="8px" className={classes.dialogTitle}>
          Запись на консультацию
        </DialogTitle>
        <Typography color={theme.color.black500} variant="body3">
          Консультант свяжется с тобой в Slack в течение трех рабочих дней для согласования времени
        </Typography>
        <DialogContent className={classes.dialogContent}>
          <form className={classes.form} onSubmit={handleSubmit(onSubmit)} id="form-consult">
            <fieldset className={classes.fieldset}>
              <ControlledSelectField
                labelTitle="Выбор темы"
                tipContent="Выбери тему"
                value={+consultantThemeValue}
                error={!!errors.consultation_theme?.message}
                helperText={errors.consultation_theme?.message || ' '}
                onChange={handleConsultantThemeChange}
                items={consultants}
                maxWidth={304}
              />
              <ControlledTextField
                labelTitle="Описание проблемы"
                fieldName="problem_description"
                control={control}
                error={!!errors.problem_description?.message}
                helperText={errors.problem_description?.message || ' '}
                placeholder="Опиши проблему"
                multiline
                rows={4}
                maxWidth={306}
                marginLeft="-2px"
              />
            </fieldset>
          </form>
          <Button form="form-consult" sx={{ width: 306, minHeight: 50 }} type="submit">
            Отправить заявку
          </Button>
        </DialogContent>
      </Dialog>
    </ThemeProvider>
  )
}

export default AppointmentConsultationPopup
