import React, { useEffect } from 'react'
import { Box, Typography } from '@mui/material'
import useStyles from './WeekTasks.style'
import TaskProgress from '../TaskProgress/TaskProgress'
import MetricsAlert from '../MetricsAlert/MetricsAlert'

interface IWeekTasksProps {
  data?: {
    responses_count?: number
    week_reports?: number
    contacts_added?: number
  }
}

const WeekTasks = ({ data }: IWeekTasksProps) => {
  const classes = useStyles()
  const responses = data?.responses_count ?? 0
  const weekReports = data?.week_reports ?? 0
  const contactsAdded = data?.contacts_added ?? 0
  const isAlertSuccess = responses > 9 && weekReports > 0 && contactsAdded > 2
  const alertMessage = isAlertSuccess
    ? 'Ты молодец, так держать! Все задачи завершены, но это не повод останавливаться. Продолжай делать отклики, и работа не заставит себя ждать'
    : 'Тебе необходимо каждую неделю выполнять поставленные задачи  для того, чтобы оставаться в программе Акселерации'

  return (
    <Box className={classes.root}>
      <Typography variant="h3" component="h2">
        Задачи на неделю
      </Typography>
      <ul className={classes.tasksList}>
        <li className={classes.task}>
          <TaskProgress current={responses} maximum={10} title="Отклики" />
        </li>
        <li className={classes.task}>
          <TaskProgress current={weekReports} maximum={1} title="Запись в Ленте" />
        </li>
        <li className={classes.task}>
          <TaskProgress current={contactsAdded} maximum={3} title="Добавить контакты" />
        </li>
      </ul>
      <MetricsAlert message={alertMessage} />
    </Box>
  )
}

export default React.memo(WeekTasks)
