import React, { useEffect, useState } from 'react'
import { Typography } from '@material-ui/core'
import { parseISO, format } from 'date-fns'
import { ru } from 'date-fns/locale'
import { ResponsiveContainer, AreaChart, CartesianGrid, XAxis, YAxis, Area, Tooltip, Legend } from 'recharts'
import { Box } from '@mui/material'
import useStyles from './EventsStats.styles'
import SwiperButtons from '../../../../Sub/Buttons/SwiperButtons/SwiperButtons'
import { IEventsStats } from '../../../../../services/types/metrics/metrics-types'

interface IProps {
  eventsStats: Array<IEventsStats>
}

const EventsStats = ({ eventsStats }: IProps) => {
  const classes = useStyles()
  const [data, setData] = useState<Array<IEventsStats>>([])

  const getNormalizedData = () => {
    const normalizedData = eventsStats.map((date) => ({
      ...date,
      date: format(parseISO(date.date), 'dd MMM', { locale: ru }),
    }))

    setData(normalizedData)
  }

  const renderColorfulLegendText = (value: string) => <span style={{ color: '#797981' }}>{value}</span>

  useEffect(() => {
    if (eventsStats) getNormalizedData()
  }, [eventsStats])

  return (
    <div className={classes.root}>
      <div className={classes.titleContainer}>
        <Box className={classes.titleAndButtonContainer}>
          <Typography variant="h3">Статистика событий</Typography>
          <SwiperButtons />
        </Box>
        <Typography variant="body2" color="textSecondary">
          За последние 14 дней
        </Typography>
      </div>
      <ResponsiveContainer minHeight={312}>
        <AreaChart
          width={560}
          height={312}
          data={data}
          margin={{
            top: 10,
            right: 20,
            left: -40,
            bottom: 0,
          }}
        >
          <defs>
            <linearGradient id="g1" x1="0" y1="0" x2="0" y2="1">
              <stop offset="0%" stopColor="hsla(141, 40%, 83%, 1)" stopOpacity={1} />
              <stop offset="100%" stopColor="hsla(141, 40%, 83%, 1)" stopOpacity={0} />
            </linearGradient>
            <linearGradient id="g2" x1="0" y1="0" x2="0" y2="1">
              <stop offset="5%" stopColor="hsla(253, 54%, 61%, 1)" stopOpacity={1} />
              <stop offset="95%" stopColor="hsla(253, 54%, 61%, 1)" stopOpacity={0} />
            </linearGradient>
            <linearGradient id="g3" x1="0" y1="0" x2="0" y2="1">
              <stop offset="0%" stopColor="hsla(33, 100%, 79%, 1)" stopOpacity={1} />
              <stop offset="100%" stopColor="hsla(33, 100%, 79%, 1)" stopOpacity={0} />
            </linearGradient>
            <linearGradient id="g4" x1="0" y1="0" x2="0" y2="1">
              <stop offset="0%" stopColor="hsla(216, 100%, 68%, 1)" stopOpacity={1} />
              <stop offset="100%" stopColor="hsla(216, 100%, 68%, 1)" stopOpacity={0} />
            </linearGradient>
          </defs>

          <CartesianGrid strokeDasharray="3 3" vertical={false} />
          <XAxis dataKey="date" axisLine={false} tickLine={false} />
          <YAxis allowDecimals={false} tickLine={false} axisLine={false} />
          <Tooltip />
          <Area
            type="monotone"
            name="Отклики"
            dataKey="response"
            stroke="hsla(141, 40%, 83%, 1)"
            fillOpacity={1}
            fill="url(#g1)"
          />
          <Area
            name="Тестовые"
            type="monotone"
            dataKey="test_task"
            stroke="hsla(253, 54%, 61%, 1)"
            fillOpacity={1}
            fill="url(#g2)"
          />
          <Area
            name="Технические собеседования"
            type="monotone"
            dataKey="tech_interview"
            stroke="hsla(33, 100%, 79%, 1)"
            fill="url(#g3)"
          />
          <Area name="Офферы" type="monotone" dataKey="offer" stroke="hsla(216, 100%, 68%, 1)" fill="url(#g4)" />
          <Legend
            formatter={renderColorfulLegendText}
            verticalAlign="bottom"
            iconType="circle"
            height={60}
            wrapperStyle={{ bottom: -60, left: 15 }}
          />
        </AreaChart>
      </ResponsiveContainer>
    </div>
  )
}

export default EventsStats
