import React, { useEffect, useState } from 'react'
import { styled } from '@mui/material/styles'
import {
  Box,
  Dialog,
  DialogContent,
  LinearProgress,
  Typography,
  linearProgressClasses,
  IconButton,
} from '@mui/material'

import { IconClose } from '../../../../assets/Icons'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { closeActiveAchievement } from '../../../../services/slices/achievements-slice'

const BorderLinearProgress = styled(LinearProgress)(() => ({
  height: 12,
  borderRadius: 8,
  [`&.${linearProgressClasses.colorPrimary}`]: {
    backgroundColor: '#DDE0E4',
  },
  [`& .${linearProgressClasses.bar}`]: {
    borderRadius: 5,
    backgroundColor: '#5A9BFF',
  },
}))

const AchievementPopup = () => {
  const dispatch = useAppDispatch()
  const { selectedAchievement } = useAppSelector((state) => state.achievements)
  const { title, message, image, currentProgress, maxProgress, open } = selectedAchievement
  const normalise = (value?: number) => (maxProgress && value ? (value * 100) / maxProgress : 0)
  const handleClose = () => dispatch(closeActiveAchievement())

  return (
    <Dialog
      open={open}
      onClose={handleClose}
      sx={{
        '& .MuiDialog-paper': {
          width: 623,
          minWidth: 623,
          maxWidth: 623,
          overflow: 'hidden',
          padding: '24px 20px',
        },
      }}
    >
      <IconButton
        onClick={handleClose}
        sx={{
          position: 'absolute',
          right: 18,
          top: 17,
        }}
      >
        <IconClose />
      </IconButton>
      <DialogContent sx={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
        <img src={image} alt="" style={{ maxWidth: 240, maxHeight: 240 }} />
        <Box
          sx={{
            width: 315,
            minHeight: 177,
            maxHeight: 240,
            overflow: 'hidden',
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'space-between',
          }}
        >
          <Box sx={{ paddingRight: '10px' }}>
            <Typography variant="h2">{title}</Typography>
            <Typography variant="body1" sx={{ color: '#797981', fontSize: 16, margin: '8px 0 16px' }}>
              {message}
            </Typography>
          </Box>
          <Box sx={{ maxWidth: 264, marginBottom: '32px' }}>
            <Box sx={{ display: 'flex', alignItems: 'baseline', marginBottom: '12px' }}>
              <Typography variant="body1" sx={{ color: '#797981', fontSize: 16 }}>
                До получения награды:&nbsp;
              </Typography>
              <Typography variant="h3" component="p" sx={{ color: '#1D6BF3', marginLeft: '8px' }}>
                {maxProgress ? `${currentProgress} / ${maxProgress}` : ''}
              </Typography>
            </Box>
            <BorderLinearProgress variant="determinate" value={normalise(currentProgress)} />
          </Box>
        </Box>
      </DialogContent>
    </Dialog>
  )
}

export default React.memo(AchievementPopup)
