import React, { useEffect, useState } from 'react'
import { Typography } from '@material-ui/core'
import { Funnel, FunnelChart } from 'recharts'
import { Box } from '@mui/material'
import useStyles from './FunnelGraph.styles'
import SwiperButtons from '../../../../Sub/Buttons/SwiperButtons/SwiperButtons'
import { IFunnelStats } from '../../../../../services/types/metrics/metrics-types'

type TArrayKeys = ['response', 'hr_interview', 'test_task', 'tech_interview', 'boss_interview', 'offer', 'rejection']
type TNormalizeDate = {
  axisValue: number | undefined | ''
  fill: string
  name: string
  stroke: string
  value: number | undefined
}

const normalizedTitle = (title: string): string => {
  switch (title) {
    case 'response':
      return 'Отклики'

    case 'hr_interview':
      return 'Собеседования с HR'
    case 'test_task':
      return 'Тестовые задания'
    case 'tech_interview':
      return 'Тех. собеседования'
    case 'boss_interview':
      return 'Собеседования с руководителем'
    case 'offer':
      return 'Офферы'
    default:
      return ''
  }
}

const normalizedFill = (title: string) => {
  switch (title) {
    case 'response':
      return 'hsla(218, 90%, 53%, 0.4)'
    case 'hr_interview':
      return 'hsla(218, 90%, 53%, 0.5)'
    case 'test_task':
      return 'hsla(218, 90%, 53%, 0.6)'
    case 'tech_interview':
      return 'hsla(218, 90%, 53%, 0.7)'
    case 'boss_interview':
      return 'hsla(218, 90%, 53%, 0.8)'
    case 'offer':
      return 'hsla(218, 90%, 53%, 0.85)'
    default:
      return ''
  }
}

interface IProps {
  funnelStats: IFunnelStats | null
}

const FunnelGraph = ({ funnelStats }: IProps) => {
  const classes = useStyles()
  const [data, setData] = useState<Array<TNormalizeDate>>([])

  const getNormalizedData = () => {
    const rawData = funnelStats
      ? { ...funnelStats }
      : {
          response: 0,
          hr_interview: 0,
          test_task: 0,
          tech_interview: 0,
          boss_interview: 0,
          offer: 0,
          rejection: 0,
        }
    delete rawData.rejection
    const arrayData = Object.keys(rawData) as TArrayKeys
    const normalizedData: Array<TNormalizeDate> = arrayData.reduce(
      (previousValue: Array<TNormalizeDate>, currentValue) => {
        const result: TNormalizeDate = {
          value: rawData[currentValue],
          name: currentValue,
          fill: normalizedFill(currentValue),
          stroke: 'hsla(218, 90%, 53%, 1)',
          axisValue: rawData[currentValue],
        }
        previousValue.push(result)
        return previousValue
      },
      [],
    )
    normalizedData.push({
      value: 0,
      name: '',
      fill: 'hsla(218, 90%, 53%, 0.95)',
      stroke: 'hsla(218, 90%, 53%, 1)',
      axisValue: 0,
    })
    // Сдвигаем
    for (let i = 0; i < normalizedData.length; i += 1) {
      if (normalizedData[i + 1]) {
        normalizedData[i + 1].value = Number(normalizedData[i].axisValue)
      } else {
        break
      }
    }
    // Стираем осевое значение у последнего поля, чтобы ноликом не светить
    normalizedData[normalizedData.length - 1].axisValue = ''
    setData(normalizedData)
  }

  useEffect(() => {
    // if (funnelStats) {
    getNormalizedData()
    // }
  }, [funnelStats])

  return (
    <div className={classes.root}>
      <div className={classes.titleContainer}>
        <Box className={classes.titleAndButtons}>
          <Typography variant="h3">Общая воронка</Typography>
          <SwiperButtons />
        </Box>
        <Typography variant="body2" color="textSecondary">
          За все время
        </Typography>
      </div>
      <div className={classes.graphContainer}>
        <div>
          {data.map((val) => (
            <div title={normalizedTitle(val.name)} className={classes.axisDash} key={val.name}>
              <p className={classes.axisName}>{normalizedTitle(val.name)}</p>
            </div>
          ))}
        </div>
        <div className={classes.funnelWrap}>
          <FunnelChart margin={{ top: 0, right: 0, bottom: 0, left: 0 }} width={410} height={363} data={data}>
            <Funnel
              dataKey="value"
              data={data}
              isAnimationActive
              trapezoids={[{ x: 0, y: 0, upperWidth: 360, lowerWidth: 32, height: 53 }]}
            />
          </FunnelChart>
        </div>
        <div>
          {data.map((val) => (
            <div className={classes.axis} key={val.name}>
              <p className={classes.axisName}>{val.axisValue}</p>
            </div>
          ))}
        </div>
      </div>
    </div>
  )
}

export default FunnelGraph
