import React from 'react'
import { styled } from '@mui/material/styles'
import { Box, Typography, LinearProgress, linearProgressClasses } from '@mui/material'
import { useAppDispatch } from '../../../../services/store/hooks'
import { openActiveAchievement } from '../../../../services/slices/achievements-slice'
import { TAchievement } from '../../../../services/types/achievements/achievements-types'

const BorderLinearProgress = styled(LinearProgress)(() => ({
  height: 4,
  borderRadius: 4,
  [`&.${linearProgressClasses.colorPrimary}`]: {
    backgroundColor: '#DDE0E4',
  },
  [`& .${linearProgressClasses.bar}`]: {
    borderRadius: 4,
    backgroundColor: '#5A9BFF',
  },
}))

interface IAchievemnetItemProps {
  data: TAchievement
}

const AchievementItem = ({ data }: IAchievemnetItemProps) => {
  const dispatch = useAppDispatch()
  const { title, message, image, currentProgress, maxProgress } = data
  const normalise = (value?: number) => (maxProgress && value ? (value * 100) / maxProgress : 0)

  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    event.stopPropagation()
    dispatch(openActiveAchievement(data))
  }

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        padding: '22px 13px 2px',
        transition: 'all 0.3s ease-in-out',
        '&:hover': {
          backgroundColor: '#F1F6FF',
          cursor: 'pointer',
        },
      }}
      onClick={handleClick}
    >
      <img src={image} alt="" style={{ flexBasis: 67, maxHeight: 67 }} />
      <Box
        sx={{
          width: '100%',
          minHeight: 87,
          maxHeight: 87,
          overflow: 'hidden',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'space-between',
        }}
      >
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'space-between',
            padding: '2px 7px 0 10px',
            alignItems: 'center',
          }}
        >
          <Box>
            <Typography variant="body3">{title}</Typography>
            <Typography variant="body1" sx={{ color: '#797981', fontSize: 16 }}>
              {message}
            </Typography>
          </Box>
          <Typography variant="h3" component="p" sx={{ color: '#1D6BF3', marginLeft: '8px' }}>
            {maxProgress ? `${currentProgress} / ${maxProgress}` : ''}
          </Typography>
        </Box>
        <Box sx={{ padding: '2px 7px 21px 10px' }}>
          <BorderLinearProgress variant="determinate" value={normalise(currentProgress)} />
        </Box>
      </Box>
    </Box>
  )
}

export default React.memo(AchievementItem)
