import React, { Dispatch, FC, SetStateAction, useState } from 'react'
import { Box, Button, IconButton, Typography } from '@mui/material'
import { useSwiper } from 'swiper/react/swiper-react'
import useStyles from './InterviewAndConsultationSlide.style'
import theme from '../../../../utils/themeMUI5'
import onboardingImage from '../../../../assets/onboardingImage.svg'
import IconSwiperMetricsArrow from '../../../../assets/Icons/IconSwiperMetricsArrow/IconSwiperMetricsArrow'
import { useAppSelector } from '../../../../services/store/hooks'
import SwiperButtons from '../../../Sub/Buttons/SwiperButtons/SwiperButtons'
import { TSetSnack } from '../../../../services/types/global-types'
import AppointmentConsultationPopup from '../../MainPage/MainActionsList/AppointmentConsultationPopup/AppointmentConsultationPopup'
import AppointmentInterviewPopup from '../../MainPage/MainActionsList/AppointmentInterviewPopup/AppointmentInterviewPopup'

type Props = {
  variant: 'left' | 'right'
  setSnack: TSetSnack
}

const InterviewAndConsultationSlide: FC<Props> = ({ variant, setSnack }) => {
  const classes = useStyles()
  const swiper = useSwiper()
  const [isHoverLeft, setIsHoverLeft] = useState(false)
  const { profile } = useAppSelector((store) => store.auth)
  const [openInterviewPopup, setOpenInterviewPopup] = useState(false)
  const [openConsultationPopup, setOpenConsultationPopup] = useState(false)
  const haveAccess = profile?.status === 'Акселерация'
  const isLeft = variant === 'left'

  const onMouseEnterHandler = (setter: Dispatch<SetStateAction<boolean>>) => {
    setter(true)
  }
  const onMouseLeaveHandler = (setter: Dispatch<SetStateAction<boolean>>) => {
    setter(false)
  }
  const buttonHandler = () => {
    if (isLeft) {
      setOpenInterviewPopup(true)
    } else {
      setOpenConsultationPopup(true)
    }
  }

  return (
    <Box className={classes.slide}>
      <Typography mb="8px" color={theme.color.black900} variant="body4">
        {isLeft ? 'Записаться на собеседование' : 'Записаться на консультацию'}
      </Typography>
      <Typography
        variant="body2"
        className={haveAccess ? `${classes.subtitle}` : `${classes.subtitle} ${classes.subtitleDenied}`}
      >
        {/* eslint-disable-next-line no-nested-ternary */}
        {haveAccess
          ? isLeft
            ? 'Тренируй навыки прохождения собеседований и получи подробный фидбек от практикующих HR и разработчика'
            : 'Получи ответы на все вопросы о трудоустройстве: от стратегии поиска до личных страхов и синдрома самозванца'
          : 'Этот формат станет доступен при переходе на этап Акселерация. Напиши менеджеру по трудоустройству  на своём курсе для уточнения твоего текущего этапа'}
      </Typography>
      {haveAccess ? (
        <Button onClick={buttonHandler} type="reset">
          {isLeft ? 'Хочу на собеседование' : 'Хочу на консультацию'}
        </Button>
      ) : (
        <SwiperButtons />
      )}
      <Box className={classes.image} component="img" src={onboardingImage} alt="Изображение человека под лампой" />
      {haveAccess && (
        <IconButton
          onMouseEnter={() => onMouseEnterHandler(setIsHoverLeft)}
          onMouseLeave={() => onMouseLeaveHandler(setIsHoverLeft)}
          onClick={() => swiper.slideNext()}
          className={classes.rightButton}
        >
          <IconSwiperMetricsArrow isHover={isHoverLeft} />
        </IconButton>
      )}
      <AppointmentInterviewPopup open={openInterviewPopup} setOpen={setOpenInterviewPopup} setSnack={setSnack} />
      <AppointmentConsultationPopup
        open={openConsultationPopup}
        setOpen={setOpenConsultationPopup}
        setSnack={setSnack}
      />
    </Box>
  )
}

export default React.memo(InterviewAndConsultationSlide)
