import React from 'react'
import { Box } from '@mui/material'
import StatInfo from '../StatInfo/StatInfo'

interface IWeekStatsProps {
  data?: { hr_interviews?: number; partner_vacancies?: number; diary_entries?: number }
}

const WeekStats = ({ data }: IWeekStatsProps) => (
  <Box sx={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gridColumnGap: 10, gridRowGap: 12 }}>
    <StatInfo type="diary-records" value={data?.diary_entries ?? 0} />
    <StatInfo type="hr-interviews" value={data?.hr_interviews ?? 0} />
    <StatInfo type="partners-vacancies" value={data?.partner_vacancies ?? 0} />
    <StatInfo type="achievements" />
  </Box>
)

export default React.memo(WeekStats)
