import React, { useEffect, useState } from 'react'
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Typography } from '@mui/material'
import { useAppDispatch } from '../../../../services/store/hooks'
import { setNotificated } from '../../../../services/slices/metrics-slice'

interface IMetricsPopupProps {
  data: {
    title: string
    text: string
    image: string
    is_notificated: boolean
    button_title: string
  } | null
}

const MetricsPopup = ({ data }: IMetricsPopupProps) => {
  if (!data) return null
  const dispatch = useAppDispatch()
  const { title, text, image, is_notificated, button_title } = data
  const [open, setOpen] = useState<boolean>(false)

  useEffect(() => {
    if (open) dispatch(setNotificated())
  }, [open])

  useEffect(() => {
    if (!is_notificated) setOpen(true)
  }, [is_notificated])

  return (
    <Dialog
      open={open}
      onClose={() => setOpen(false)}
      sx={{
        '& .MuiDialog-paper': {
          width: 386,
          minWidth: 386,
          maxWidth: 386,
          overflow: 'hidden',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'space-between',
        },
      }}
    >
      <DialogTitle>{title}</DialogTitle>
      <DialogContent>
        <Typography variant="body1" sx={{ color: '#797981', fontSize: 16, margin: '8px 0 16px' }}>
          {text}
        </Typography>
        <img src={image} alt="" style={{ margin: '20px 0' }} />
      </DialogContent>
      <DialogActions>
        <Button
          onClick={() => setOpen(false)}
          type="button"
          color="primary"
          sx={{ minHeight: 50, width: '100%', fontSize: 16 }}
        >
          {button_title}
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default React.memo(MetricsPopup)
