import React, { useState } from 'react'
import { Box } from '@mui/material'
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react'
import { AxiosError } from 'axios'
import useStyles from './AdviceSlider.style'
import AdviceSlide from '../AdviceSlide/AdviceSlide'
import useAsync from '../../../../hooks/useAsync'
import api from '../../../../api/api'
import { API_ADVICE_ENDPOINT } from '../../../../utils/constants'

type TAdvice = {
  title: string
  id: number
  content: string
}

type TAdviceResponseElement = {
  description: string
  id: number
  name: string
  url: string
  url_description: string
}

const AdviceSlider = () => {
  const classes = useStyles()
  const [advice, setAdvice] = useState<Array<TAdvice> | null>(null)
  const { run } = useAsync({
    data: [],
    error: null,
  })
  const getAdvice = async () => {
    try {
      const response = await api.get(API_ADVICE_ENDPOINT)
      setAdvice(
        response.data.map((r: TAdviceResponseElement, index: number) => ({
          id: index + 1,
          title: r.name,
          content: r.description,
        })),
      )
      return Promise.resolve(response.data)
    } catch (e) {
      const err = e as AxiosError
      return Promise.reject(err.response)
    }
  }

  React.useEffect(() => {
    run(getAdvice())
  }, [])

  return (
    <Box className={classes.slider}>
      <Swiper loop initialSlide={1}>
        {advice?.map((item) => (
          <SwiperSlide key={item.id}>
            <AdviceSlide
              title={item.title}
              content={item.content}
              currentNumber={item.id}
              totalAdvices={advice.length}
            />
          </SwiperSlide>
        ))}
      </Swiper>
    </Box>
  )
}

export default AdviceSlider
