import React, { useEffect, useState } from 'react'
import { Box, CircularProgress, CircularProgressProps, Typography } from '@mui/material'
import { IconProgressSuccess } from '../../../../assets/Icons'

interface ICircularProgressWithLabelProps extends CircularProgressProps {
  value: number
  current: number
  maximum: number
  title: string
}

interface ITaskProgressProps {
  current: number
  maximum: number
  title: string
}

const CircularProgressWithLabel = ({ value, current, maximum, title, ...props }: ICircularProgressWithLabelProps) => (
  <Box display="flex" alignItems="center" flexDirection="column">
    <Box sx={{ position: 'relative', width: 140, height: 140, marginBottom: '17px' }}>
      <CircularProgress
        variant="determinate"
        value={100}
        {...props}
        size={140}
        thickness={1.55}
        sx={{ top: 0, left: 0, bottom: 0, right: 0, position: 'absolute', color: '#DDE0E4' }}
      />
      <CircularProgress
        variant="determinate"
        value={value}
        {...props}
        size={140}
        thickness={1.55}
        sx={{
          top: 0,
          left: 0,
          bottom: 0,
          right: 0,
          position: 'absolute',
          color: current / maximum < 1 ? '#5A9BFF' : '#87CC9E',
        }}
      />
      <Box
        sx={{
          top: 0,
          left: 0,
          width: 140,
          height: 140,
          position: 'absolute',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
        }}
      >
        {current / maximum < 1 ? (
          <Typography variant="h1" component="span" sx={{ letterSpacing: -0.9 }}>
            {current}&nbsp;/&nbsp;{maximum}
          </Typography>
        ) : (
          <IconProgressSuccess />
        )}
      </Box>
    </Box>
    <Typography variant="body3" component="h3">
      {title}
    </Typography>
  </Box>
)

const TaskProgress = ({ current, maximum, title }: ITaskProgressProps) => {
  const [progress, setProgress] = useState<number>(0)
  const normalizedCurrentValue = current < maximum ? current : maximum
  const maxValue = (normalizedCurrentValue * 100) / maximum

  useEffect(() => {
    const timer = setInterval(() => {
      setProgress((prevProgress) => (prevProgress >= maxValue ? maxValue : prevProgress + 1))
    }, 10)
    return () => {
      clearInterval(timer)
    }
  }, [current])

  return <CircularProgressWithLabel value={progress} current={normalizedCurrentValue} maximum={maximum} title={title} />
}

export default React.memo(TaskProgress)
