import React, { Dispatch, SetStateAction, useState } from 'react'
import { Box, IconButton } from '@mui/material'
import { useSwiper } from 'swiper/react/swiper-react'
import IconSwiperMetricsArrow from '../../../../assets/Icons/IconSwiperMetricsArrow/IconSwiperMetricsArrow'
import useStyles from './SwiperButtons.style'

const SwiperButtons = () => {
  const classes = useStyles()
  const swiper = useSwiper()
  const [isHoverLeft, setIsHoverLeft] = useState(false)
  const [isHoverRight, setIsHoverRight] = useState(false)

  const onMouseEnterHandler = (setter: Dispatch<SetStateAction<boolean>>) => {
    setter(true)
  }
  const onMouseLeaveHandler = (setter: Dispatch<SetStateAction<boolean>>) => {
    setter(false)
  }

  return (
    <Box className={classes.swiperButtons}>
      <IconButton
        onMouseEnter={() => onMouseEnterHandler(setIsHoverLeft)}
        onMouseLeave={() => onMouseLeaveHandler(setIsHoverLeft)}
        onClick={() => swiper.slidePrev()}
        className={classes.swiperButton}
      >
        <IconSwiperMetricsArrow isHover={isHoverLeft} left />
      </IconButton>
      <IconButton
        onMouseEnter={() => onMouseEnterHandler(setIsHoverRight)}
        onMouseLeave={() => onMouseLeaveHandler(setIsHoverRight)}
        onClick={() => swiper.slideNext()}
        className={classes.swiperButton}
      >
        <IconSwiperMetricsArrow isHover={isHoverRight} left={false} />
      </IconButton>
    </Box>
  )
}

export default SwiperButtons
