import React, { useEffect, useState } from 'react'
import { Paper, Typography } from '@mui/material'
import bookImage from '../../../../assets/book.svg'
import interviewImage from '../../../../assets/interview.svg'
import partnersImage from '../../../../assets/partners.svg'
import medalImage from '../../../../assets/medal.svg'

interface IStatInfoProps {
  type: 'diary-records' | 'hr-interviews' | 'partners-vacancies' | 'achievements'
  value?: number
}

const StatInfo = ({ type, value }: IStatInfoProps) => {
  let icon
  let title
  switch (type) {
    case 'diary-records':
      icon = bookImage
      title = 'Откликов за неделю'
      break
    case 'hr-interviews':
      icon = interviewImage
      title = 'HR-интервью за неделю'
      break
    case 'partners-vacancies':
      icon = partnersImage
      title = 'Откликов к Партнёрам'
      break
    case 'achievements':
      icon = medalImage
      title = 'Получено достижений'
      break
    default:
      icon = bookImage
      title = 'Записей в Дневнике'
      break
  }

  return (
    <Paper
      sx={{
        width: 285,
        height: 206,
        borderRadius: '10px',
        outline: '1px solid #DDE0E4',
        padding: '36px 44px 28px 32px',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
      }}
      elevation={0}
    >
      <img src={icon} alt="" style={{ width: 62, height: 62, alignSelf: 'flex-end' }} />
      <div>
        <Typography variant={value || value === 0 ? 'h1' : 'h3'} component="p" sx={{ marginTop: '19px' }}>
          {value ?? 'В разработке'}
        </Typography>
        <Typography variant="body" component="h2" sx={{ marginTop: '4px', fontWeight: 500, color: '#797981' }}>
          {title}
        </Typography>
      </div>
    </Paper>
  )
}

export default React.memo(StatInfo)
