import React, { useEffect } from 'react'
import { Box, ThemeProvider } from '@mui/material'
import theme from '../../../utils/themeMUI5'
import useStyles from './MetricsPage.style'
import {
  WeekStats,
  WeekTasks,
  MetricsPopup,
  GraphsSlider,
  InterviewAndConsultationsSlider,
  AdviceSlider,
  AchievementsList,
} from '../../../components/Main/MetricsPage'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import { getProfile } from '../../../services/axios/auth'
import { getNotifications, getStats } from '../../../services/axios/metrics'
import { TSetSnack } from '../../../services/types/global-types'
import { getAchievements } from '../../../services/axios/achievements'
import { resetMetrics } from '../../../services/slices/metrics-slice'
import { resetAchievements } from '../../../services/slices/achievements-slice'

type IMetricsPageProps = {
  setSnack: TSetSnack
}

const MetricsPage = ({ setSnack }: IMetricsPageProps) => {
  const dispatch = useAppDispatch()
  const { profile } = useAppSelector((store) => store.auth)
  const { notification, eventsStats, funnelStats } = useAppSelector((store) => store.metrics)

  const classes = useStyles()
  const stats =
    profile?.status === 'Сообщество' ? (
      <WeekStats data={profile?.student_tasks} />
    ) : (
      <WeekTasks data={profile?.student_tasks} />
    )

  useEffect(() => {
    dispatch(getProfile())
    dispatch(getStats())
    dispatch(getAchievements())
    return () => {
      dispatch(resetMetrics())
      dispatch(resetAchievements())
    }
  }, [])

  useEffect(() => {
    if (profile?.student_tasks?.popup_url) dispatch(getNotifications(profile?.student_tasks?.popup_url))
  }, [profile?.student_tasks?.popup_url])

  return (
    <ThemeProvider theme={theme}>
      <MetricsPopup data={notification} />
      <div className={classes.root}>
        <InterviewAndConsultationsSlider setSnack={setSnack} />
        <AdviceSlider />
        <GraphsSlider funnelStats={funnelStats} eventsStats={eventsStats} />
        {stats}
        <Box sx={{ gridColumn: '1 / 3' }}>
          <AchievementsList />
        </Box>
      </div>
    </ThemeProvider>
  )
}

export default React.memo(MetricsPage)
