import React from 'react'
import { Box } from '@mui/material'
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react'
import useStyles from './GraphsSlider.style'
import FunnelGraph from '../../MainPage/Charts/FunnelGraph/FunnelGraph'
import EventsStats from '../../MainPage/Charts/EventsStats/EventsStats'
import { IEventsStats, IFunnelStats } from '../../../../services/types/metrics/metrics-types'

interface IProps {
  funnelStats: IFunnelStats | null
  eventsStats: Array<IEventsStats>
}

const GraphsSlider = ({ funnelStats, eventsStats }: IProps) => {
  const classes = useStyles()
  return (
    <Box className={classes.container}>
      <Swiper loop>
        <SwiperSlide>
          <FunnelGraph funnelStats={funnelStats} />
        </SwiperSlide>
        <SwiperSlide>
          <EventsStats eventsStats={eventsStats} />
        </SwiperSlide>
      </Swiper>
    </Box>
  )
}

export default GraphsSlider
