/* eslint-disable no-param-reassign */
import React from 'react'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import { Typography, IconButton, InputAdornment, FormHelperText } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import DatePicker from '../../../../Sub/CustomDatePicker/CustomDatePicker'
import IconClose from '../../../../../assets/Icons/IconClose/IconClose'
import useStyles from './AppointmentInterviewPopup.styles'
import api from '../../../../../api/api'
import useAsync from '../../../../../hooks/useAsync'
import { newInterviewSchema } from '../../../../../utils/schemas'
import IconCalendar from '../../../../../assets/Icons/IconCalendar/IconCalendar'
import { API_INTERVIEWS_ENDPOINT } from '../../../../../utils/constants'
import { formatApiDate } from '../../../../../utils/dates'
import processServerError from '../../../../../utils/server-error'

// eslint-disable-next-line camelcase
export default function AppointmentInterviewPopup({ open, setOpen, setSnack }) {
  const { run } = useAsync({
    data: null,
    error: null,
  })
  const {
    handleSubmit,
    setValue,
    setError,
    clearErrors,
    formState: { errors },
  } = useForm({ resolver: yupResolver(newInterviewSchema), mode: 'onChange' })

  const classes = useStyles()

  const [interviewDate, setInterviewDate] = React.useState(new Date())

  const handleClose = () => {
    setOpen(false)
    clearErrors()
  }

  const onCreateInterview = async (data) => {
    try {
      const response = await api.post(API_INTERVIEWS_ENDPOINT, {
        ...data,
        interview_date: formatApiDate(interviewDate),
      })
      if (response.status === 201) {
        setSnack({
          open: true,
          header: 'Заявка отправлена',
          message: 'Консультант свяжется с тобой для согласования даты собеседования',
        })
        handleClose()
      }
      return Promise.resolve(response.data)
    } catch (e) {
      return processServerError(e, setError)
    }
  }

  const onSubmit = (data) => {
    run(onCreateInterview(data))
  }

  React.useEffect(() => setInterviewDate(new Date()), [])

  React.useEffect(
    () =>
      setValue('interview_date', interviewDate, {
        shouldValidate: true,
        shouldDirty: true,
      }),
    [interviewDate],
  )

  return (
    <div>
      <Dialog
        maxWidth="xl"
        classes={{ paper: classes.root }}
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <div className={classes.container}>
          <IconButton onClick={handleClose} className={classes.closePopup}>
            <IconClose />
          </IconButton>
          <DialogContent className={classes.dialogContent}>
            <form className={classes.form} onSubmit={handleSubmit(onSubmit)} id="form">
              <div className={classes.titleContainer}>
                <Typography variant="h2" id="form-dialog-title" className={classes.title}>
                  Запись на собеседование
                </Typography>
                <Typography variant="body1" color="textSecondary">
                  Публичное собеседование состоит из&#160;HR&#8209;интервью&#160;и технической части. В конце интервью
                  эксперты дают подробную обратную связь.
                </Typography>
              </div>
              <fieldset className={classes.fieldset}>
                <div className={classes.leftColumn}>
                  <div className={classes.fieldContainer}>
                    <Typography>Дата собеседования</Typography>
                    <DatePicker
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="end">
                            <IconButton className={classes.pickerButton}>
                              <IconCalendar isGray={!interviewDate} />
                            </IconButton>
                          </InputAdornment>
                        ),
                      }}
                      cancelLabel="Отменить"
                      okLabel="Сохранить"
                      inputVariant="outlined"
                      format="dd MMM yyyy"
                      disableToolbar
                      variant="dialog"
                      value={interviewDate}
                      error={errors.interview_date?.message}
                      helperText={errors.interview_date?.message || ' '}
                      onChange={(val) => {
                        if (val) {
                          setValue('interview_date', val, { shouldValidate: true, shouldDirty: true })
                          setInterviewDate(val)
                        } else {
                          setValue('interview_date', null, { shouldValidate: true, shouldDirty: true })
                          setInterviewDate(null)
                        }
                      }}
                    />
                  </div>
                </div>
              </fieldset>
            </form>
            <FormHelperText error style={{ textAlign: 'center', margin: '5px 0' }}>
              {errors.non_field_errors?.message || ' '}
            </FormHelperText>
            <div className={classes.buttonContainer}>
              <Button form="form" className={classes.submitButton} type="submit" disabled={!interviewDate}>
                Отправить запрос
              </Button>
            </div>
          </DialogContent>
        </div>
      </Dialog>
    </div>
  )
}
